* {
  /*Reset value*/
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;

  background-color: rgb(197, 241, 237);
  color: #222;
}

button {
  font-size: 1rem;
  font-weight: bold;
  background-color: rgb(197, 241, 237);

  margin: 7px 7px;
  padding: 7px 15px;
  border: none;
  border-radius: 8px;
}

span.rank {
  /*For ranks on top right*/
  position: absolute;
  top: 0;
  left: 0;

  font-size: 2rem;
  background-color: rgb(255, 128, 0);
  color: white;

  padding: 0.4rem;
  border-top-left-radius: inherit;
  border-bottom-right-radius: inherit;
}

.title {
  font-size: 2.5rem;
  color: #222;
  text-align: center;
  /* text-transform: capitalize; */
  margin-top: 3rem;
}

.booklist {
  width: 90vw;
  max-width: 1170px;
  margin: 3rem auto;
  display: grid;
  gap: 4rem;
}

@media screen and (min-width: 768px) {
  .booklist {
    grid-template-columns: repeat(3, auto);
    /* grid-template-rows: repeat(4, 100px); */
  }
}

.book {
  background-color: rgba(255, 255, 255, 0.82);
  border-radius: 1rem;
  padding: 20px;
  text-align: center;
  position: relative;
}

.book img {
  width: 75%;
  height: auto;
  object-fit: cover;
}

.book h2 {
  margin-top: 1rem;
  font-size: 1.75rem;
  font-weight: bold;
}

.book h4 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 0.8rem;
  letter-spacing: 2px;
  color: rgb(123, 123, 123);

  margin-top: 0.5rem;
}
